'use client';

import React, { useState, useEffect } from 'react';
import { useLangRouter } from '@/hooks/useLangRouter';
import { ArrowRight, Menu, X } from 'lucide-react';
import { useLanguage } from '@/context/LanguageContext';
import { motion, AnimatePresence } from 'framer-motion';

const NAV_TEXT = {
    en: { home: "Home", teachers: "For Teachers", login: "Login", apply: "Apply Now" },
    uz: { home: "Bosh Sahifa", teachers: "Ustozlar Uchun", login: "Kirish", apply: "Ariza Topshirish" }
};

export default function Header() {
    const { push, lang } = useLangRouter();
    const { toggleLanguage } = useLanguage();
    const t = NAV_TEXT[lang as 'en' | 'uz'] || NAV_TEXT.uz;
    const [scrolled, setScrolled] = useState(false);
    const [mobileMenu, setMobileMenu] = useState(false);

    useEffect(() => {
        const handleScroll = () => setScrolled(window.scrollY > 20);
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    return (
        <header
            className={`fixed top-0 left-0 w-full z-50 transition-all duration-300 ${scrolled ? 'bg-white/80 backdrop-blur-xl border-b border-gray-100 h-16' : 'bg-transparent h-24'
                }`}
        >
            <div className="container mx-auto px-6 h-full flex items-center justify-between gap-4">

                {/* 1. LOGO */}
                <div onClick={() => push('/')} className="cursor-pointer flex-shrink-0 hover:scale-105 transition-transform">
                    <img src="/logos/knowly-header.png" alt="KNOWLY" className="h-8 md:h-10 w-auto object-contain" />
                </div>

                {/* 2. CENTER NAV (Signature Gray Pill) */}
                <nav className="hidden md:flex items-center bg-gray-100/80 p-1.5 rounded-full border border-gray-200/50 backdrop-blur-md">
                    <button
                        onClick={() => push('/')}
                        className="px-6 py-2 rounded-full text-sm font-bold text-gray-500 hover:text-gray-900 transition-colors"
                    >
                        {t.home}
                    </button>
                    <button
                        onClick={() => push('/#teachers')}
                        className="px-6 py-2 rounded-full text-sm font-bold bg-white text-[#D92D20] shadow-sm shadow-gray-200 transition-all"
                    >
                        {t.teachers}
                    </button>
                </nav>

                {/* 3. RIGHT ACTIONS */}
                <div className="hidden md:flex items-center gap-3">
                    <button
                        onClick={toggleLanguage}
                        className="px-4 py-2 bg-white border border-gray-200 text-gray-700 rounded-full text-xs font-extrabold hover:border-gray-300 transition-all"
                    >
                        {lang === 'uz' ? "ðŸ‡ºðŸ‡¿ O'zb" : 'ðŸ‡¬ðŸ‡§ Eng'}
                    </button>

                    <button
                        onClick={() => push('/login')}
                        className="px-5 py-2.5 text-sm font-bold text-gray-600 hover:text-[#D92D20] transition-colors"
                    >
                        {t.login}
                    </button>

                    <button
                        onClick={() => push('/apply')}
                        className="flex items-center gap-2 bg-[#D92D20] text-white px-6 py-2.5 rounded-full font-bold shadow-lg shadow-red-200 hover:shadow-red-300 hover:scale-105 transition-all active:scale-95 text-sm"
                    >
                        <span>{t.apply}</span>
                        <ArrowRight className="w-4 h-4" />
                    </button>
                </div>

                {/* Mobile Menu Toggle */}
                <button onClick={() => setMobileMenu(true)} className="md:hidden p-2 text-gray-600">
                    <Menu className="w-6 h-6" />
                </button>
            </div>

            {/* Mobile Menu Overlay */}
            <AnimatePresence>
                {mobileMenu && (
                    <motion.div
                        initial={{ opacity: 0, x: '100%' }} animate={{ opacity: 1, x: 0 }} exit={{ opacity: 0, x: '100%' }}
                        className="fixed inset-0 bg-white z-[60] flex flex-col p-6"
                    >
                        <div className="flex justify-between items-center mb-8">
                            <img src="/logos/knowly-header.png" alt="Knowly" className="h-8" />
                            <button onClick={() => setMobileMenu(false)}><X className="w-6 h-6" /></button>
                        </div>
                        <div className="flex flex-col gap-4 text-lg font-bold">
                            <button onClick={() => { push('/'); setMobileMenu(false); }}>{t.home}</button>
                            <button onClick={() => { push('/login'); setMobileMenu(false); }} className="text-gray-600">{t.login}</button>
                            <button onClick={() => { push('/apply'); setMobileMenu(false); }} className="text-[#D92D20]">{t.apply}</button>
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </header>
    );
}
